<template>
  <div class="Upload1 pad">

    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem>{{title1}}</BreadcrumbItem>
        <BreadcrumbItem>{{title2}}</BreadcrumbItem>Upload1 - {{pad}}
      </Breadcrumb>
      <h1>{{title2}}</h1><br>
      <span>{{msg}}</span>
    </div>

    <div class="card center form">
      <Form :model="form3"  :label-width="100" >
        <FormItem label="" >
          <Input type="textarea" :rows="6" v-model="form3.input4"></Input>
        </FormItem>
        <FormItem>
          <div class="add">
            
            <Upload action="//jsonplaceholder.typicode.com/posts/">
              <Button icon="ios-cloud-upload-outline">Upload files</Button>
            </Upload>
          </div>
        </FormItem>
        <FormItem>
          <Button type="primary" :style="{width:'60%'}" @click="changeFclView"></Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Upload1',
    data () {
      return {
        title1:'',
        title2:'',
        msg:'',
        form3: {
          input1: '',
          input2: '',
        }
      }
    },
    props:['pad'],
    methods: {
      changeFclView: function () {
        this.$emit('result', 'Success2');
      },
      showMsg: function () {
        switch (this.pad) {
          default:
            this.title1 = '';
            this.title2 = '';
            this.msg = '';
            break;
          case 'fPad4':
            this.title1 = '';
            this.title2 = '';
            this.msg = '';
            break;
          case 'hPad2':
            this.title1 = '';
            this.title2 = '';
            this.msg = '';
            break;
          case 'hPad4':
            this.title1 = '';
            this.title2 = '';
            this.msg = '';
            break;
        }
      },
    },
    mounted(){
      this.showMsg();
    },
    updated(){
      this.showMsg();
    }
  }
</script>


<style scoped>
  h1{
    font-size: 28px;
    line-height: 40px;
  }
  .center{
    text-align: center;
  }
  .form{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:30px;
  }
</style>
<style>
  .Upload1 .ivu-form{
    width:500px;
  }
</style>



