<template>
  <div class="pad6 pad">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
      <h1></h1><br>
    </div>

    <div class="card" v-for="(item,index) in form.items" v-if="item.status">
      <Form :model="item"  label-position="left" :label-width="70" inline :style="{textAlign:'center'}">
        <!---->
        <div class="form-header">
          <div class="right">{{index}}
            <span v-show="!item.showIcon">
              <span @click="deletePerson">
              <Icon type="ios-trash-outline" />
              </span>&nbsp;&nbsp;
            </span>
            <span v-show="item.showIcon">
              <span @click="addPerson(index)">
              <Icon type="ios-add-circle-outline" />
              </span>
            </span>
          </div>
        </div>

        <!---->
        <div class="forms">
          <div class="form">
            <hr>
            <div class="form-card" inline>
              <div class="center">
                <FormItem label="">
                  <Input v-model="item.company"></Input>
                </FormItem>
                <FormItem label="">
                  <Input v-model="item.type"></Input>
                </FormItem>
                <FormItem label="">
                  <Input v-model="item.name"></Input>
                </FormItem>
                <FormItem label="">
                  <Input v-model="item.phone"></Input>
                </FormItem>
              </div>
              <div class="center">
                <FormItem label="">
                  <Input v-model="item.address" :style="{width:'355px'}"></Input>
                </FormItem>
                <FormItem label="">
                  <Input v-model="item.companyAddress" :style="{width:'355px'}"></Input>
                </FormItem>
              </div>
            </div>
          </div>
        </div>
        <Button type="primary" :style="{width:'18%'}" @click.native="handleSubmit" v-show="item.showButton"></Button>
      </Form>
    </div>
    <div class="showPsn card">
      <Table border :columns="columns" :data="form6.items">
        <template slot-scope="{ index}" slot="name">
          <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{ row }" slot="name">
          <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{ row, index }" slot="action">
          <Button type="primary" size="small" style="margin-right: 5px" @click="change(index)"></Button>
          <Button type="error" size="small" @click="remove(index)"></Button>
        </template>
      </Table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'pad6',
    data () {
      return {
        index:1,
        submitOk:false,
        form: {
          items:[
            {
              company: '',
              type: '',
              name: '',
              phone: '',
              address: '',
              companyAddress: '',
              status: 1,
              showButton:true,
              showIcon:true,
            },
          ]
        },//
        form6: {
          items:[
            {
              company: '',
              type: '',
              name: '',
              phone: '18765431234',
              address: '',
              companyAddress: '',
              status: 1,
              showButton:true,
              showIcon:true,
            },
          ]
        },//
        columns: [
          {
            title: '',
            type: 'index',
            width: 70,
          },
          {
            title: '',
            key: 'company',
            width: 90,
          },
          {
            title: '',
            key: 'type',
            width: 120,
          },
          {
            title: '',
            key: 'name',
            width: 100,
          },
          {
            title: '',
            key: 'phone',
            width: 120,
          },
          {
            title: '',
            key: 'address',
            width: 150,
          },
          {
            title: '',
            key: 'companyAddress',
            width:'250'
          },
          {
            title: '',
            slot: 'action',
            align: 'center'
          }
        ],

      }
    },
    methods:{
      deletePerson:function(){
        this.form.items.pop();
        let length=this.form.items.length;
        this.form.items[length-1].showButton = true;
      },
      addPerson:function(){
        let length=this.form.items.length;
        console.log(length);
        this.form.items.push({
          index:length,
          company: '',
          type: '',
          name: '',
          phone: '',
          address: '',
          companyAddress: '',
          status: 1,
          showButton:true,
          showIcon: false,
        });
        this.form.items.forEach(item => { item.showButton= false;})
        this.form.items[length].showButton = true;
      },
      handleSubmit:function(){
        this.$Modal.success({
          title: '',
          content:'',
          okText: '',
          onOk: () => {
            this.$emit('changeSystemView','Pad7');
          },
        });
      },
      change(index){
        // this.$Modal.info({
        //   title: 'User Info',
        //   content: `Name${this.data6[index].name}<br>Age${this.data6[index].age}<br>Address${this.data6[index].address}`
        // })
      },
      remove(index){
        this.form6.items.splice(index, 1);
      },
      ok(){}
    },
    mounted(){
      this.$bus.$emit('changeSmallPad','1-2');
    }
  }
</script>


<style scoped>
  .form-header{
    height:20px;
  }
  .right{
    float:right;
  }

  .center{
    display:flex;
    justify-content: center;
    width:900px;
    margin:auto;
  }
  hr{
    background-color:rgb(220, 222, 226) ;
    height:2px;
    border:0;
  }
  .form-card{
    padding:10px 50px;
    margin:auto;
  }
</style>
<style>
  .pad6 .ivu-form-item{
    margin:8px;
  }
  .pad6 .ivu-form-item-label{
    padding: 10px 2px 10px 8px;
  }
</style>



