<template>
  <div id="Message">
    <div class="card">
      <Breadcrumb :style="{margin: '12px 0'}">
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem> Message
      </Breadcrumb>
      <h1></h1><br>
      <span class="grey"></span>
    </div>
    <div class="card">
      <div class="form">
        <Form :model="msgForm" :label-width="200">
          <FormItem label=":">
            <Row :gutter="16">
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.city" placeholder="">
                  <template v-for="(item,index) in addr">
                    <Option v-bind:value="index" @click.native="getCity">{{item.name}}</Option>
                  </template>
                </Select>
              </Col>
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.county" placeholder="/" not-found-text="''" >
                  <template v-for="(item,index) in showCounties">
                    <Option v-bind:value="index" @click.native="getCounty">{{item.name}}</Option>
                  </template>
                </Select>
              </Col>
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.town"  placeholder="/" not-found-text="'/'">
                  <template v-for="(item,index) in showTowns">
                    <Option v-bind:value="index" @click.native="getTown">{{item.name}}</Option>
                  </template>
                </Select>
              </Col>
              <Col span="8" :style="{paddingLeft:0}">
                <Select v-model="msgForm.village"  placeholder="/" not-found-text="'/'">
                  <template v-for="(item,index) in showVillages">
                    <Option v-bind:value="index" @click.native="getVillage">{{item}}</Option>
                  </template>
                </Select>
              </Col>
            </Row>
          </FormItem>
          <div class="short">
            <FormItem label="">
              <Input v-model="msgForm.input4"></Input>m
            </FormItem>
            <FormItem label="">
              <Input v-model="msgForm.input5"></Input>
            </FormItem>
          </div>
          <FormItem label="">
            <Select v-model="msgForm.input6"  placeholder="">
              <template v-for="item in disaster">
                <Option v-bind:value="item">{{item}}</Option>
              </template>
            </Select>
          </FormItem>
          <FormItem>
            <Button type="primary" :style="{width:'50%'}" @click.native="handleSubmit"></Button>
          </FormItem>
        </Form>
      </div>
      <span class="grey"></span>
    </div>

  </div>
</template>
<script>
  export default {
    name: 'Message',
    data(){
      return {
        addr:[],
        showCounties:[],
        showTowns:[],
        showVillages:[],
        disaster:['','',''],
        msgForm: {
          city:'',
          county:'',
          town:'',
          village:'',
          input4: '',
          input5: '',
          input6: ''
        }
      }
    },
    methods:{
      handleSubmit:function(){
        this.$emit('changeView',"Success1");
      },
      getCity(){
        this.showCounties=this.addr[this.msgForm.city].counties;
      },
      getCounty(){
        this.showTowns=this.showCounties[this.msgForm.county].towns;
      },
      getTown(){
        this.showVillages=this.showTowns[this.msgForm.town].villages;
      },
      getVillage(){

      },

    },
    created(){
      fetch('../../static/addr.json')
        .then(response=>response.json())
        .then(json=>{
          this.addr=json;
        })
    },
    mounted(){
      this.$bus.$emit('changePad','Index');
    }
  }
</script>

<style scoped>
  .form{
    padding:50px;
    width:800px;
    /**/
    height:57vh;
    margin:auto;
  }

</style>



